*,
*::before,
*:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  font-family: sans-serif;
}

a {
  text-decoration: none;
}

.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}
@media (min-width: 1800px) {
  .container {
    width: 1800px;
  }
}

img {
  width: 100%;
  display: block;
}

.navegacion {
  background-color: #3e8de6;
  color: #eee;
  height: 50px;
  width: 100%;
  position: sticky;
  top: 0px;
  display: flex;
  justify-content: space-between;
  z-index: 10;
}

.navegacion-title {
  margin-right: 15px;
  font-size: 1.8rem;
  order: 2;
  margin-right: 15px;
}

.menu {
  position: fixed;
  top: 50px;
  background-color: #6ba8ec;
  width: 60%;
  height: 100vh;
  transition: transform 0.3s;
  transition-timing-function: cubic-bezier(0.67, -0.05, 0.57, 0.78);
  transform: translateX(-100%);
  z-index: 100;
}
.menu.active {
  transform: translateX(0);
}
.menu.active:after {
  transform: translateX(0);
  background-color: rgba(0, 0, 0, 0.45);
}
.menu:after {
  content: "";
  display: block;
  width: 66.6666666667%;
  height: 100vh;
  transform: translateX(-500%);
  position: fixed;
  top: 0;
  left: 100%;
  transition: background-color 0.3s linear 0.3s;
}

.menu-item {
  height: 70px;
  width: 100%;
  padding-left: 70px;
  padding-top: 23px;
  position: relative;
  z-index: 100;
}
.menu-item:hover {
  background-color: #4792e7;
}

.menu-link {
  font-size: 1rem;
  color: inherit;
  padding: 5px 10px;
}

.menu-bar {
  margin-left: 15px;
  padding-top: 6px;
  cursor: pointer;
  order: 1;
  margin-left: 15px;
}
.menu-bar:hover [class^=bar-] {
  background-color: #d5d5d5;
}
.menu-bar:hover [class$=top] {
  transform: translateY(-3.5px) scale(1.1, 1.1);
}
.menu-bar:hover [class$=medium] {
  transform: scale(0.9, 0.9);
}
.menu-bar:hover [class$=bottom] {
  transform: translateY(3.5px) scale(1.1, 1.1);
}
.menu-bar.active [class$=top] {
  transform: scale(1.2, 1.2) translateY(10px) rotate(45deg);
}
.menu-bar.active [class$=medium] {
  position: absolute;
  background-color: transparent;
}
.menu-bar.active [class$=bottom] {
  transform: scale(1.2, 1.2) translateY(2.5px) rotate(-45deg);
}

[class^=bar-] {
  width: 30px;
  height: 5px;
  background-color: white;
  margin-top: 5px;
  transition-timing-function: cubic-bezier(0.67, -0.05, 0.57, 0.78);
  transition: background-color 0.3s, transform 0.3s;
}

@media (min-width: 768px) {
  .navegacion {
    position: relative;
  }
  .navegacion-title {
    margin-right: 0;
    margin-left: 15px;
    order: 1;
  }
  .navegacion .menu-bar {
    position: absolute;
    opacity: 0;
  }
  .navegacion .menu {
    display: flex;
    height: initial;
    width: initial;
    position: relative;
    top: 0;
    transform: translateX(0);
    background-color: transparent;
    margin-right: 15px;
    margin-left: 0;
    order: 2;
  }
  .navegacion .menu:after {
    content: none;
  }
  .navegacion .menu-item {
    display: flex;
    justify-content: center;
    align-items: center;
    height: initial;
    width: initial;
    padding-top: 0;
    padding-left: 10px;
    padding-right: 10px;
  }
  .navegacion .menu-link {
    margin: 0;
  }
}
@media (min-width: 1800px) {
  .navegacion {
    width: 1800px;
    margin-left: auto;
    margin-right: auto;
  }
}
.banner {
  width: 100%;
  height: 300px;
  overflow: hidden;
}

.slider {
  animation: slider 15s infinite;
  animation-timing-function: steps(1, jump-start);
}
@keyframes slider {
  33.3333333333% {
    transform: translate3D(0%, 0, 0);
  }
  66.6666666667% {
    transform: translate3D(-100%, 0, 0);
  }
  100% {
    transform: translate3D(-200%, 0, 0);
  }
}

.imagen {
  position: absolute;
  z-index: 1;
  width: 100%;
  max-width: 1800px;
}
.imagen:before {
  position: absolute;
  z-index: 1;
  content: "";
  display: block;
  background-color: rgba(6, 6, 6, 0.3);
  width: 100%;
  height: 500px;
  top: 0;
  left: 0;
}
.imagen:nth-child(0) {
  transform: translate3D(0%, 0, 0);
}
.imagen:nth-child(1) {
  transform: translate3D(100%, 0, 0);
}
.imagen:nth-child(2) {
  transform: translate3D(200%, 0, 0);
}
.imagen img {
  max-width: 1800px;
  height: 300px;
  display: block;
  object-fit: cover;
}
.imagen:nth-child(1) .content-imagen {
  transform: translate3D(-25%, 0, 0);
}
.imagen:nth-child(2) .content-imagen {
  transform: translate3D(25%, 0, 0);
}
.imagen:nth-child(3) .content-imagen {
  transform: translate3D(0, 25%, 0);
}

.content-imagen {
  position: absolute;
  z-index: 1;
  color: white;
  top: 15%;
  left: 10%;
  font-size: 30px;
  width: 70%;
  opacity: 0;
  animation: text 5s infinite;
}
@keyframes text {
  10%, to {
    transform: translate3D(0, 0, 0);
    opacity: 1;
  }
}

.imagen-title {
  font-size: 1.7em;
}

.imagen-text {
  font-size: 0.6em;
}

[class^=imagen-] {
  position: relative;
  z-index: 1;
}

@media (min-width: 768px) {
  .banner, .imagen img {
    height: 500px;
  }

  .content-imagen {
    top: 30%;
  }
}
.section {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  background-color: #474747;
  padding: 20px 10px;
}
.section-img {
  width: 90%;
  padding: 50px 20px;
  margin-left: 5%;
  margin-right: 5%;
}

.info-part {
  display: grid;
  grid-template: repeat(2, 1fr)/repeat(2, 1fr);
  color: white;
}

.info {
  position: relative;
  padding: 30px 0px 20px 0px;
}
.info:nth-child(1) {
  border-right: 2px solid #2e2e2e;
}
.info:nth-child(3) {
  border-top: 2px solid #2e2e2e;
  border-right: 2px solid #2e2e2e;
}
.info:nth-child(4) {
  border-top: 2px solid #2e2e2e;
}
.info [class*=icon-] {
  width: 25px;
  height: 25px;
  padding: 4px 0;
  position: absolute;
  top: 5px;
  left: calc(50% - 10px);
  border-radius: 20px;
  border: 1px solid black;
  transition: color background border 0.3s linear;
}
.info:hover [class*=icon-] {
  color: #89ed89;
  background: white;
  border: 2px solid #89ed89;
}
.info-title {
  font-size: 1.6rem;
}
.info-text {
  font-size: 0.9rem;
}
.info-text, .info-title,
.info [class*=icon-] {
  text-align: center;
  color: #a6a6a6;
  transition: color 0.3s linear;
}
.info:hover .info-text {
  color: white;
}
.info:hover .info-title {
  color: #89ed89;
}

@media (min-width: 768px) {
  .info {
    padding: 30px 10px 20px 10px;
  }

  .section {
    grid-template: 1fr/repeat(2, 1fr);
  }
  .section-img {
    width: 80%;
    margin-left: 10%;
    margin-right: 10%;
  }

  .info-part {
    padding-left: 20px;
  }
}
@media (min-width: 1200px) {
  .info {
    padding: 30px 20px 20px 20px;
  }

  .section-img {
    width: 60%;
    margin-left: 20%;
    margin-right: 20%;
  }
}
.banner-static {
  position: relative;
  color: #eee;
}

.banner-img {
  position: relative;
}
.banner-img [class*=icon-] {
  position: absolute;
  top: 20%;
  left: 50%;
  display: block;
  transform: scale3D(0, 0, 1);
  transform-origin: center center center;
  animation: icon 0.5s cubic-bezier(0, 0, 0, 1.5) 0.7s forwards;
}
@keyframes icon {
  to {
    transform: scale3D(2.5, 2.5, 1);
  }
}
.banner-img [class*=icon-]:after {
  content: "";
  border: solid 1px currentColor;
  display: block;
  width: 24px;
  height: 24px;
  position: absolute;
  top: 2px;
  left: -4px;
  transform: translate3D(0, -4px, 0) rotateZ(45deg);
  opacity: 0;
  animation: load 0.5s linear forwards;
}
@keyframes load {
  to {
    opacity: 1;
    transform: translate3D(0, -4px, 0) rotateZ(45deg) scale3D(1.25, 1.25, 1);
  }
}
.banner-img img {
  height: 400px;
  object-fit: cover;
}
.banner-img:before {
  content: "";
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(21, 21, 21, 0.35);
}

.banner-content {
  width: 90%;
  position: absolute;
  top: 40%;
  left: 10%;
  color: currentColor;
}

.banner-title {
  font-size: 24px;
  padding-bottom: 15px;
  display: block;
}

.banner-text {
  font-size: 16px;
}

.banner-title,
.banner-text,
.banner-btn {
  text-align: center;
}

@media (min-width: 768px) {
  .banner-content {
    width: 100%;
    left: 0;
  }
}
.banner-btn {
  background-color: #FF6C6B;
  color: #eee;
  display: table;
  padding: 4px 6px;
  margin: 15px auto;
  border-radius: 5px;
  font-size: 16px;
}

.medium {
  background-color: #474747;
  padding-top: 35px;
  padding-bottom: 15px;
  width: 100%;
}
@media (min-width: 1800px) {
  .medium {
    width: 1800px;
    margin-right: auto;
    margin-left: auto;
  }
}
.medium .medium_title {
  text-align: center;
  color: white;
  font-size: 1.5em;
}
.medium .medium_text {
  color: #a6a6a6;
  width: 70%;
  margin-left: auto;
  margin-right: auto;
}

.project {
  display: grid;
  grid-template: auto auto auto auto/1fr;
  grid-template-areas: " project-1" " project-2" " project-3" "image";
  grid-row-gap: 10px;
  grid-column-gap: 5px;
  padding: 25px 45px;
  background-color: #474747;
}
@media (min-width: 768px) {
  .project {
    grid-template: auto auto auto/1fr 1fr;
    grid-template-areas: "image project-1" "image project-2" "image project-3";
  }
}
.project_image {
  grid-area: image;
  display: block;
  width: 70%;
  margin: auto;
}
.project_box {
  display: table;
  padding: 15px 10px;
  border-radius: 8px;
  box-shadow: 0 0 0 rgba(255, 255, 255, 0.05), 0 0 0 rgba(0, 0, 0, 0.5), inset -5px -5px 10px rgba(255, 255, 255, 0.05), inset 5px 5px 15px rgba(0, 0, 0, 0.5);
}
.project_box_title {
  color: white;
  font-size: 1.5em;
  text-align: center;
  display: block;
  padding-top: 15px;
  padding-bottom: 10px;
  transition: color 0.5s;
}
.project_box_text {
  color: #a6a6a6;
  display: block;
  padding: 5px 25px;
  transition: color 0.5s;
}
.project_box:hover .project_box_title {
  color: #3483dd;
}
.project_box:hover .project_box_text {
  color: white;
}
.project_box:nth-child(1) {
  grid-area: project-1;
}
.project_box:nth-child(2) {
  grid-area: project-2;
}
.project_box:nth-child(3) {
  grid-area: project-3;
}

#about {
  background-color: #474747;
  padding: 20px 10px;
}
#about .map {
  display: block;
  width: 70%;
  margin-right: auto;
  margin-left: auto;
}

.footer {
  padding: 30px 15px;
  color: white;
  text-align: center;
}